<template>
    <!--        回复-->
    <div class="reply_box" :class="[$store.state.theme == 'night' ? 'reply_box_night':'']">
        <div class="reply">
            <input type="text" class="reply_content" :class="[$store.state.theme == 'night' ? 'reply_content_night':'']" v-model="reply_content">
            <svg xmlns="http://www.w3.org/2000/svg" width="29.25" height="29.25" viewBox="0 0 29.25 29.25">
                <path id="Icon_ionic-md-happy" data-name="Icon ionic-md-happy" d="M18,3.375A14.625,14.625,0,1,0,32.625,18,14.621,14.621,0,0,0,18,3.375ZM18,29.7A11.7,11.7,0,1,1,29.7,18,11.735,11.735,0,0,1,18,29.7Zm5.119-13.163a2.194,2.194,0,1,0-2.194-2.194A2.154,2.154,0,0,0,23.119,16.538Zm-10.237,0a2.194,2.194,0,1,0-2.194-2.194A2.154,2.154,0,0,0,12.881,16.538ZM18,26.044a7.968,7.968,0,0,0,7.459-5.119H10.541A7.968,7.968,0,0,0,18,26.044Z" transform="translate(-3.375 -3.375)" fill="#5ab4f0"/>
            </svg>
<!--          <div>  <el-button v-show="reply_content!=''" type="primary" round size="mini" >发送</el-button></div>-->
<!--            <van-button @click="user_comment(comment_key)" round type="primary" size="small" :disabled="reply_content==''">发&nbsp;送</van-button>-->
            <svg @click="user_comment(comment_key)" xmlns="http://www.w3.org/2000/svg" width="30" height="30" viewBox="0 0 30 30">
                <path id="路径_213" data-name="路径 213" d="M0,30,3.12,16.11l17.25-1.08L3.12,13.86,0,0,30,15Z" transform="translate(0 30) rotate(-90)" fill="#5ab4f0"/>
            </svg>
        </div>
    </div>
</template>

<script>
    import { Dialog } from 'vant';
    import ajax from '../http'
    export default {
        name: "Reply",
        data () {
            return{
                reply_content: ''
            }
        },
        props:{
            comment_key:Object
        },
        methods:{
           async user_comment(data){
               if (this.$store.state.user_cookie !=''&&this.reply_content!=''){
                   // type=6，评论动态
                   if (data.type == 6){
                       await ajax(
                           this.$store.state.api_url+'comment',
                           {t:1,type:data.type,threadId:data.threadId,content:this.reply_content,cookie:this.$store.state.user_cookie},
                           'post'
                       ).then((res)=>{
                           console.log(res);
                           this.$emit('push_user_comment',this.reply_content)
                           this.reply_content = ''
                       }).catch((err)=>{
                           console.log(err);
                       })
                   }else
                       {
                       await ajax(
                           this.$store.state.api_url+'comment',
                           {t:1,type:data.type,id:data.id,content:this.reply_content,cookie:this.$store.state.user_cookie},
                           'post'
                       ).then((res)=>{
                           console.log(res);
                           this.$emit('push_user_comment',this.reply_content)
                           this.reply_content = ''
                       }).catch((err)=>{
                           console.log(err);
                       })
                   }
               }else if (this.$store.state.user_cookie =='') {
                   Dialog.alert({
                       message: '登录后评论',
                   }).then(() => {
                       // on close
                       // console.log('777')
                   });
               }
            }
        }
    }
</script>

<style scoped>
    .reply_box{
        height:4.75rem;
        width: 100%;
        background-color: #F6F6F6;
        display: flex;
        align-items: center;
    }
    .reply{
        width: 100%;
        height: 3.5rem;
        /*background-color: white;*/
        display: flex;
        justify-content: space-evenly;
        margin-top: 0.5rem;
    }
    .reply_content{
        width: 14.5rem;
        height: 1.75rem;
        border-radius: 1rem;
        font-size: 13px;
        border: none;
        /*border:1px solid black;*/
        background-color: #FFFFFF;
    }
    /*input:focus{*/
    /*    outline: none;*/
    /*    border-color: #409eff;*/
    /*}*/

    .reply_box_night{
        width: 100%;
        height:4.75rem;
        background-color: #1C1C1C;
        color: white;
    }
    .reply_content_night{
        width: 14.5rem;
        height: 1.75rem;
        border-radius: 1rem;
        font-size: 13px;
        border: none;
        /*border:1px solid black;*/
        background-color: #282828;
    }
</style>