<template>
    <div class="box" :class="[$store.state.theme == 'night' ? 'box_night':'']">
    <div class="Singer_Information" :class="[$store.state.theme == 'night' ? 'Singer_Information_night':'']" ref="slide" v-if="singer_name.length>0">
        <div class="Singer_image_box">
            <img :src="singer_image" class="Singer_image">
            <div class="Singer_material_box">
            <span class="Singer_name">{{ singer_name }}</span>
            </div>
        </div>
        <div class="information_classification">
            <Filter :content="classify" @boxClick="cut"></Filter>
        </div>
        <div  class="Singer_information_box">
            <!--        歌手资料-->
            <div v-show="cut_page == '主页'" class="Singer_information_box_in">
                <div class="Singer_information_title">歌手资料</div>
               <div class="singer_information_name">{{ singer_name }}</div>
                <div class="Singer_introduction">歌手介绍：</div>
                <div class="Singer_introduction_briefDesc">{{"&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;"+singer_information }}</div>
                <div style="height: 1rem;width: 100%"></div>
            </div>
<!--            歌手音乐-->
            <div v-show="cut_page == '音乐'" class="Singer_Music_box">
                <Single :Song-data="singer_song" @boxClick="open_song_menu"></Single>
                <div class="leave_blank"></div>
            </div>

<!--            歌手视频-->
            <div class="singer_video_box" v-show="cut_page == '视频'">
                <SingerVideo :Video-result="singer_mv" v-if="singer_mv!=''"></SingerVideo>
            </div>
        </div>
    </div>
    <!--        歌手音乐菜单-->
    <div class="song_menu" v-show="$store.state.operation_panel" @click="colse_song_menu">
        <OperationPanel :operation_song_information="operation_song_information"></OperationPanel>
    </div>

        <div style="position: fixed;bottom: 0;z-index: 9;width: 100%">
            <Menu1 :menu_show="1"></Menu1>
        </div>
        <div style="position: fixed; top:3.5rem;left:1rem;z-index: 9">
            <Back :color="1"></Back>
        </div>
    </div>
</template>

<script>
    import Menu1 from "../../components/Menu1";
    import Back from '../../components/menu/Back'
    import ajax from "../../http";
    import Filter from "../../components/home/Filter";
    import Single from "../../components/mine/SingleTwo";
    import SingerVideo from "../../components/home/SingerVideo";
    import OperationPanel from "../../components/home/OperationPanel";
    export default {
        name: "SingerInformation",
        components: {
            SingerVideo,
            Single,
            Filter,
            OperationPanel,Back,Menu1
        },
        computed:{
            opacity:function () {

                return 0
            }
        },
        data() {
          return{
              classify:[
                  {
                      id:0,
                      types:'主页'
                  },
                  {
                      id:1,
                      types:'音乐'
                  },
                  {
                      id:2,
                      types:'视频'
                  },
              ],
              k:'',
              transparency:'',
              cut_page:'主页',
              singer_name:'',
              singer_image:'',
              singer_song:[],
              singer_mv:[],
              singer_information:[],
              operation_song_information:[],
              follow:false
          }
        },
        methods: {
            touchmove(){
                this.transparency = -((this.$refs.slide.getBoundingClientRect().top+130)/100-1.3)
                console.log(this.$refs.slide.getBoundingClientRect().top)
            },
            cut(data){
                this.cut_page = data
                console.log('999')
            },
            kkp(){
               this.follow = ! this.follow
            },
            colse_song_menu(){
                this.$store.commit('hide_operation_panel')
            },
            // 歌曲菜单栏 获取歌曲粗略信息
            open_song_menu(data){
                this.operation_song_information = data
            },
           //  //关注歌手
           // async focus_on_singers(){
           //      let id = this.$route.params.id
           //      this.follow = ! this.follow
           //     await ajax(
           //          this.$store.state.api_url+'follow',
           //          {id:id,t:1,cookie:this.$store.state.user_cookie},
           //          'post'
           //      ).then((res)=>{
           //          console.log(res);
           //      }).catch((err)=>{
           //          console.log(err);
           //      })
           //  },
           //  //取消关注
           // async cancel_attention(){
           //     let id = this.$route.params.id
           //     this.follow = ! this.follow
           //     await ajax(
           //         this.$store.state.api_url+'follow',
           //         {id:id,t:0,cookie:this.$store.state.user_cookie},
           //         'post'
           //     ).then((res)=>{
           //         console.log(res);
           //     }).catch((err)=>{
           //         console.log(err);
           //     })
           //      }

        },
        mounted() {

            let id = this.$route.params.id

            Promise.all([
                ajax(
                    // 'http://127.0.0.1:3000/artist/detail?id='+id
                    this.$store.state.api_url+'artist/detail?id='+id
                ),
                ajax(
                    // 'http://127.0.0.1:3000/artists?id='+id
                    this.$store.state.api_url+'artists?id='+id
                ),
                ajax(
                    // 'http://127.0.0.1:3000/artist/mv?id='+id
                    this.$store.state.api_url+'artist/mv?id='+id
                )

            ]).then((res)=>{
                console.log(res)
                // 歌曲结果
                this.singer_name = res[0].data.artist.name
                this.singer_image = res[0].data.artist.cover
                // 歌手信息
                this.singer_information = res[0].data.artist.briefDesc
                // 歌手结果
                for (let i = 0;i<res[1].hotSongs.length;i++){
                    this.singer_song.push({
                        song_name:res[1].hotSongs[i].name,
                        song_id:res[1].hotSongs[i].id,
                        song_singer:res[1].artist.name,
                        song_image:res[1].hotSongs[i].al.picUrl,
                    })
                }

                // 视频结果
                for (let i=0;i<res[2].mvs.length;i++){
                    this.singer_mv.push({
                        video_id:res[2].mvs[i].id,
                        video_image:res[2].mvs[i].imgurl,
                        video_name:res[2].mvs[i].name,
                        video_user_name:res[2].mvs[i].artistName,
                    })
                }

            }).catch((error)=>{
                console.log(error)
            })


        }

    }
</script>

<style scoped>
    *{
        font-family: 宋体;
    }
.Singer_Information{
    position: absolute;
    top: 0;
    width: 100%;
    height: 130%;
    background-color: white;
    z-index: 1;
}
.Singer_Information_head{
    width: 100%;
    height: 5rem;
    position: absolute;
    top: 0;
    z-index: 1;
    background-color: white;
    display: flex;
    justify-content: center;
    font-size: 20px;
    opacity:0;

}
.Singer_Information_head span{
    margin-top: 3rem;
}
.Singer_Information_head_white{
    width: 100%;
    height: 5rem;
    background-color: white;

}
.Singer_image_box{
    width: 100%;
    height: 18.75rem;
    position: relative;
    top: 0;
    background-color: #409eff;
    z-index: 2;
}
    .Singer_image{
        width: 100%;
        height: 18.75rem;
        position: absolute;
        top: 0;
        z-index: 1;
    }
    .Singer_material_box{
        width: 100%;
        position: absolute;
        z-index: 1;
        display: flex;
        flex-direction: column;


    }
    .Singer_name{
        font-size: 40px;
        margin-top: 11rem;
        color: white;
        margin-left: 1rem;
        font-weight: bold;
    }
    .information_classification{
        margin-top: 1rem;
    }
.Singer_information_box{
    width:100% ;
    display: flex;
    justify-content: center;

}
.Singer_information_box_in{
    width: 95%;
    max-height: 22rem;
    /*border: 1px solid black;*/
    border-radius: 1rem;
    margin-top: 1rem;
    overflow-y:auto;
    /*阴影*/
    -webkit-box-shadow: #DDDDDD 0px 0px 10px;
    -moz-box-shadow: #DDDDDD 0px 0px 10px;
    box-shadow: #DDDDDD 0px 0px 10px;
}
    .Singer_information_title{
        margin-top: 1rem;
        margin-left: 1rem;
        font-size: 18px;
    }
    .Singer_introduction{
        margin-top: 0.5rem;
        margin-left: 1rem;
        font-size: 15px;
    }
    .Singer_Music_box{
        width: 95%;
        max-height: 22rem;
        border-radius: 1rem;
        margin-top: 1rem;
        /*阴影*/
        -webkit-box-shadow: #DDDDDD 0px 0px 10px;
        -moz-box-shadow: #DDDDDD 0px 0px 10px;
        box-shadow: #DDDDDD 0px 0px 10px;
        overflow-y: scroll;

    }
    .singer_information_name{
        margin-left: 2.5rem;
        margin-top: 1rem;
        margin-bottom: 1rem;
        font-size: 20px;
    }
    .Singer_introduction_briefDesc{
        margin-top: 1rem;
        width: 90%;
        margin-left: 5%;
    }
    .song_menu{
        width: 100%;
        height: 100%;
        /*height: 100%;*/
        position: absolute;
        top: 0;
        z-index: 13;

    }
    .song_menu_background{
        width: 100%;
        height: 100%;
       /*height: 800px;*/
        position: absolute;
        top: 0;
        z-index: 13;
        background-color: black;
    }
    .box{
        width: 100%;
        height: 100%;
        position: fixed;
        display: flex;
        flex-direction: column;

    }
.singer_video_box{
    overflow-y:auto;
    width: 100%;
    mxa-height: 22rem;
    /*阴影*/
    -webkit-box-shadow: #DDDDDD 0px 0px 10px;
    -moz-box-shadow: #DDDDDD 0px 0px 10px;
    box-shadow: #DDDDDD 0px 0px 10px;
}
    .follow_btn{
        width: 4rem;
        height: 2rem;
        background-color: #409eff;
        border-radius: 0.75rem;
        text-align: center;
        line-height: 2rem;
        color: white;
    }
.follow_btn_{
    width: 4rem;
    height: 2rem;
    background-color: #9E9E9E;
    border-radius: 0.75rem;
    text-align: center;
    line-height: 2rem;
    color: white;
}

    .box_night{
        width: 100%;
        height: 100%;
        position: fixed;
        background-color: #222225;
        color: white;

    }
    .Singer_Information_night{
        position: absolute;
        top: 0;
        width: 100%;
        height: 130%;
        background-color: #222225;
        z-index: 1;
    }


</style>